import * as actionType from './actionType'
import {getData} from 'util'
import {API_SHIPPEDORDERS_PAGE, API_SHIPPEDORDERS_SHIPPEDTRACES, API_SHIPPEDORDERS_EXPORT, API_SHIPPEDORDERS_DISTRIBUTION, API_SHIPPEDORDERS_CONFIRMSHIPPED, API_SHIPPED_LOADCOUNTS, API_POST_PURCHASE_ORDER_UPDATESTATUSBYORDERCODE, API_GET_MENUS_PERMISSIONS_BANS} from 'api'
import moment from 'moment'

//同步
export const update = (object) => ({
  type: actionType.OUTWAREHOUSELISTALLOT_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData, status) => (dispatch) => {
  dispatch({type: actionType.OUTWAREHOUSELISTALLOT_UPDATE, data: {tableLoading: true}})
  let reqObjSearchParams = {...reqData.searchParams, orderType: 'D'}
  if (reqObjSearchParams.planDate) {
    reqObjSearchParams.planDate = moment(reqObjSearchParams.planDate).format('YYYY-MM-DD')
  }

  for (let i in reqObjSearchParams) {
    if (Object.prototype.toString.call(reqObjSearchParams[i]) === '[object Object]') {
      i === 'productName' ? reqObjSearchParams[i] = reqObjSearchParams[i].children : reqObjSearchParams[i] = reqObjSearchParams[i].value
      // reqObjSearchParams[i] = reqObjSearchParams[i].value
    }
  }
  if (reqObjSearchParams.initDate && reqObjSearchParams.initDate.length) {
    reqObjSearchParams.createTimeStart = moment(reqObjSearchParams.initDate[0]).format('YYYY-MM-DD HH:mm:ss')
    reqObjSearchParams.createTimeEnd = moment(reqObjSearchParams.initDate[1]).format('YYYY-MM-DD HH:mm:ss')
  }
  if (reqObjSearchParams.initDate2 && reqObjSearchParams.initDate2.length) {
    reqObjSearchParams.sendDateStart = moment(reqObjSearchParams.initDate2[0]).format('YYYY-MM-DD')
    reqObjSearchParams.sendDateEnd = moment(reqObjSearchParams.initDate2[1]).format('YYYY-MM-DD')
  }
  delete reqObjSearchParams.initDate2
  delete reqObjSearchParams.initDate
  reqData.searchParams = reqObjSearchParams

  if (status) {
    getData({api: API_SHIPPEDORDERS_PAGE, ...reqData}).then(data => {
      // 相应的逻辑判断
      let {page, size} = reqData.searchParams
      dispatch({
        type: actionType.OUTWAREHOUSELISTALLOT_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          selectedRowKeys: [], // 选中的队列key
          selectedRows: [], // 选中的队列数据
          searchResult: data.result.list || []
        }
      })
    }).catch(err => {
      dispatch({
        type: actionType.OUTWAREHOUSELISTALLOT_FILE
      })
    })
  } else {
    // 查询统计数量tabs
    getData({api: API_SHIPPED_LOADCOUNTS, ...reqData}).then(({result}) => {
      // 相应的逻辑判断
      let arr = ['0', '1', '2', '3', '4', '5', '6', '8', '9']
      let status = ''
      let tabNumObj = {
        tab0: '',
        tab1: '',
        tab2: '',
        tab3: '',
        tab4: '',
        tab5: '',
        tab6: '',
        tab8: '',
        tab9: ''
      }
      result.map((o) => {
        tabNumObj[`tab${o.key}`] = o.value
      })
      for (let i = 0; i < arr.length; i++) {
        for (let j = 0; j < result.length; j++) {
          if (result[j].key == arr[i] && result[j].value) {
            status = arr[i]
            break
          }
        }
        if (status) break
      }
      if (status) {
        reqData.searchParams.status = status
      }
      getData({api: API_SHIPPEDORDERS_PAGE, ...reqData}).then(data => {
        // 相应的逻辑判断
        let {page, size} = reqData.searchParams
        dispatch({
          type: actionType.OUTWAREHOUSELISTALLOT_UPDATE,
          data: {
            page,
            size,
            tableLoading: false,
            total: data.result.total,
            tableActiveKey: status,
            selectedRowKeys: [], // 选中的队列key
            selectedRows: [], // 选中的队列数据
            searchResult: data.result.list || [],
            ...tabNumObj
          }
        })
      }).catch(err => {
        dispatch({
          type: actionType.OUTWAREHOUSELISTALLOT_FILE
        })
      })
    }).catch(err => {
      dispatch({
        type: actionType.OUTWAREHOUSELISTALLOT_FILE
      })
    })
  }
}

// 获取入库单操作记录
export const handleGetDetailRecord = (reqData) => (dispatch) => {
  dispatch({type: actionType.OUTWAREHOUSELISTALLOT_UPDATE, data: {deliveryTableLoading: true}})
  getData({api: API_SHIPPEDORDERS_SHIPPEDTRACES, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.OUTWAREHOUSELISTALLOT_UPDATE,
      data: {
        deliveryTableLoading: false,
        searchResultRecord: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.OUTWAREHOUSELISTALLOT_FILE
    })
  })
}

// 一键出库订单详情
export const handleGetDetailDelivery = (reqData) => (dispatch) => {
  dispatch({type: actionType.OUTWAREHOUSELISTALLOT_UPDATE, data: {deliveryTableLoading: true}})
  getData({api: API_SHIPPEDORDERS_DISTRIBUTION, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.OUTWAREHOUSELISTALLOT_UPDATE,
      data: {
        deliveryTableLoading: false,
        modalData: data.result || {}
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.OUTWAREHOUSELISTALLOT_FILE
    })
  })
}

// 确认出库
export const handleStock = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.OUTWAREHOUSELISTALLOT_UPDATE, data: {deliveryTableLoading: true}})
  let api = API_SHIPPEDORDERS_CONFIRMSHIPPED
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.OUTWAREHOUSELISTALLOT_UPDATE,
      data: {
        deliveryTableLoading: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.OUTWAREHOUSELISTALLOT_FILE
    })
  })
}

// 导出
export const handleExport = (reqData, callBack) => (dispatch) => {
  let reqObjSearchParams = {...reqData.searchParams, orderType: 'D'}
  if (reqObjSearchParams.planDate) {
    reqObjSearchParams.planDate = moment(reqObjSearchParams.planDate).format('YYYY-MM-DD')
  }
  for (let i in reqObjSearchParams) {
    if (Object.prototype.toString.call(reqObjSearchParams[i]) === '[object Object]') {
      reqObjSearchParams[i] = reqObjSearchParams[i].value
    }
  }
  if (reqObjSearchParams.initDate && reqObjSearchParams.initDate.length) {
    reqObjSearchParams.createTimeStart = moment(reqObjSearchParams.initDate[0]).format('YYYY-MM-DD HH:mm:ss')
    reqObjSearchParams.createTimeEnd = moment(reqObjSearchParams.initDate[1]).format('YYYY-MM-DD HH:mm:ss')
  }
  if (reqObjSearchParams.initDate2 && reqObjSearchParams.initDate2.length) {
    reqObjSearchParams.sendDateStart = moment(reqObjSearchParams.initDate2[0]).format('YYYY-MM-DD')
    reqObjSearchParams.sendDateEnd = moment(reqObjSearchParams.initDate2[1]).format('YYYY-MM-DD')
  }
  delete reqObjSearchParams.initDate2
  delete reqObjSearchParams.initDate
  reqData.searchParams = reqObjSearchParams
  getData({api: API_SHIPPEDORDERS_EXPORT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.INVENTORYDETAIL_FILE
    })
  })
}

// 取消订单
export const handlecancelOrder = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.OUTWAREHOUSELISTALLOT_UPDATE, data: {tableLoading: true}})
  let api = API_POST_PURCHASE_ORDER_UPDATESTATUSBYORDERCODE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.OUTWAREHOUSELISTALLOT_UPDATE,
      data: {
        tableLoading: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.OUTWAREHOUSELISTALLOT_FILE
    })
  })
}

// 获取页面按钮权限
export const GetButtonPower = (reqData) => (dispatch) => {
  getData({api: API_GET_MENUS_PERMISSIONS_BANS, ...reqData}).then(({data}) => {
    console.log(data)
    // 相应的逻辑判断
    let Power = {}
    data.map((o) => {
      Power[o.menuCode] = true
    })
    dispatch({
      type: actionType.OUTWAREHOUSELISTALLOT_UPDATE,
      data: {
        ...Power
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.OUTWAREHOUSELISTALLOT_FILE
    })
  })
}



