import React, {PureComponent} from 'react'
import {
  Table,
  Icon,
  Popover
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import JsBarcode from 'jsbarcode'

@connect(
  state => ({...state.outWarehouseListAllot, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '操作',
      width: 150,
      dataIndex: 'defaultStorageLocationFlag',
      key: 'defaultStorageLocationFlag',
      render: (text, record, index) => <div className="tableOperation">
        {
          record.status == 0 && <span onClick={() => {
            this.onEdit('isShowDeliveryModal', record)
          }}>一键分配出库</span>
        }
        <span onClick={() => {
          const {update} = this.props
          update({isShowRecordModal: true, modalId: record.orderCode})
        }}>记录</span>
      </div>
    }, {
      title: '出库单',
      width: 150,
      dataIndex: 'orderCode',
      key: 'orderCode',
      render: (text, record, index) => <React.Fragment><a onClick={() => {
        window.open(`/wms/out-warehouse/outWarehouseList/det/${record.orderCode}`)
      }}>{text}</a><Popover content={<img id={`key${text}`} style={{width: '221px', height: '50px'}} />} title={text} trigger="click"><Icon type="file-text" className='my-icon' onClick={this.handlePrint.bind(this, text)} /></Popover></React.Fragment>
    }, {
      title: '业务类型',
      width: 150,
      dataIndex: 'businessTypeDesc',
      key: 'businessTypeDesc'
    }, {
      title: '状态',
      width: 100,
      dataIndex: 'statusDesc',
      key: 'statusDesc'
    }, {
      title: '波次号',
      width: 150,
      dataIndex: 'waveCode',
      key: 'waveCode'
    }, {
      title: '全量出库',
      width: 100,
      dataIndex: 'allShippedFlagDesc',
      key: 'allShippedFlagDesc'
    }, {
      title: '分配短缺',
      width: 100,
      dataIndex: 'distributeShortageFlagDesc',
      key: 'distributeShortageFlagDesc'
    }, {
      title: '出库单打印次数',
      width: 150,
      dataIndex: 'printTimes',
      key: 'printTimes'
    }, {
      title: '包裹单数',
      width: 100,
      dataIndex: 'packagesNumber',
      key: 'packagesNumber'
    }, {
      title: '创建人',
      width: 100,
      dataIndex: 'createBy',
      key: 'createBy'
    }, {
      title: '创建时间',
      width: 150,
      dataIndex: 'createTime',
      key: 'createTime'
    }, {
      title: '发货时间',
      width: 150,
      dataIndex: 'sendTime',
      key: 'sendTime'
    }, {
      title: '计划发货日期',
      width: 150,
      dataIndex: 'planDate',
      key: 'planDate'
    }, {
      title: '来源订单',
      width: 150,
      dataIndex: 'originOrderCode',
      key: 'originOrderCode'
    }, {
      title: '关联操作订单',
      width: 150,
      dataIndex: 'linkOperateOrderCode',
      key: 'linkOperateOrderCode'
    }, {
      title: '关联出入库单',
      width: 150,
      dataIndex: 'linkInOutOrderCode',
      key: 'linkInOutOrderCode'
    }, {
      title: '关联订单',
      width: 150,
      dataIndex: 'linkOrderCode',
      key: 'linkOrderCode'
    }, {
      title: '外部单号',
      width: 150,
      dataIndex: 'externalOrderCode',
      key: 'externalOrderCode'
    }, {
      title: '自动执行',
      width: 100,
      dataIndex: 'autoStatus',
      key: 'autoStatus'
    }]
  }
  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }
  // 打印条形码
  handlePrint = (text) => {
    setTimeout(() => {
      JsBarcode(`#key${text}`, text, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
    })
  }
  //一些操作
  onEdit = (modalType, record) => {
    const {update} = this.props
    update({[modalType]: true, modalId: [record.id], modalData: {}})
  }

  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size, tableActiveKey} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, status: tableActiveKey, ...searchParams, isAsc: true, columns: sorter.field}}, true)
      } else {
        handleGetList({searchParams: {page, size, status: tableActiveKey, ...searchParams, isAsc: false, columns: sorter.field}}, true)
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, status: tableActiveKey, ...searchParams}}, true)
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult,
      selectedRowKeys
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    let rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeys, selectedRows})
      },
      selectedRowKeys
      // getCheckboxProps: record => ({
      //   disabled: record.status !== 6 && record.status !== 7 && record.status !== 8 // Column configuration not to be checked
      // })
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



