import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Tabs
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {setVariant} from 'Variant'
import {getNowPageId} from '../../global/components/menuData'
const {TabPane} = Tabs

@connect(
  state => ({...state.outWarehouseListAllot}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, window.location.pathname)
    GetButtonPower({urlParams: [ID]})
  }
  /* 打印预览 */
  handleBrowse = () => {
    let {selectedRows} = this.props
    if (!selectedRows.length) {
      message.error('请勾选订单')
      return
    }
    selectedRows = selectedRows.filter((o) => o.status == 6 || o.status == 7 || o.status == 8)
    if (!selectedRows.length) {
      message.error('出库后才能打印出库单')
      return
    }
    let ids = []
    selectedRows.map((o) => {
      ids.push(o.id)
    })
    setVariant('wms/outWarehouseList', ids)
    window.open('/wms/outWarehouseList/Print', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
  }
  // 导出
  handleExport = () => {
    const {handleExport, searchParams, page, size, tableActiveKey} = this.props
    handleExport({searchParams: {page, size, orderType: 'D', status: tableActiveKey, ...searchParams, productName: searchParams.productName ? searchParams.productName.children : ''}}, () => {
      message.success('导出成功')
    })
  }
      // 查询数据
  query = (tableActiveKey) => {
    const {handleGetList, searchParams, size, update} = this.props
    let reqObj = {...searchParams}
    update({tableActiveKey})
    handleGetList({searchParams: {...reqObj, page: 1, status: tableActiveKey, size}}, true)
  }

  // 取消订单
  handlecancelOrder = () => {
    const {selectedRows, handlecancelOrder, tableActiveKey} = this.props
    if (!selectedRows.length) {
      message.error('请勾选订单')
      return
    }
    let orderList = []
    selectedRows.map((o) => {
      orderList.push(o.originOrderCode)
    })
    handlecancelOrder({searchParams: {orderCode: orderList.join(','), status: '5'}}, () => {
      message.success('操作成功')
      const {handleGetList, searchParams, page, size} = this.props
      handleGetList({searchParams: {page, size, status: tableActiveKey, ...searchParams}}, true)
    })
  }

  render() {
    const {tableActiveKey, tab0, tab1, tab2, tab3, tab4, tab5, tab6, tab8, tab9, outWarehouseListAllotExport, outWarehouseListAllotPrint} = this.props
    return (
      <div className={style['table-tool']} style={{paddingBottom: 0}}>
        {
          outWarehouseListAllotExport && <Button type="primary" className={style.btn} onClick={this.handleExport}>导出</Button>
        }
        {
          outWarehouseListAllotPrint && <Button type="primary" className={style.btn} onClick={this.handleBrowse}>打印出库单</Button>
        }
        {
          tableActiveKey == '0' && <Button type="primary" className={style.btn} onClick={this.handlecancelOrder}>取消订单</Button>
        }
        <Tabs onChange={this.query} activeKey={tableActiveKey} >
          <TabPane tab={`初始(${tab0 || 0})`} key="0"></TabPane>
          <TabPane tab={`已集波(${tab1 || 0})`} key="1"></TabPane>
          <TabPane tab={`分配中(${tab2 || 0})`} key="2"></TabPane>
          <TabPane tab={`已分配(${tab3 || 0})`} key="3"></TabPane>
          <TabPane tab={`拣货中(${tab4 || 0})`} key="4"></TabPane>
          <TabPane tab={`拣货完成(${tab5 || 0})`} key="5"></TabPane>
          <TabPane tab={`已发货(${tab6 || 0})`} key="6"></TabPane>
          <TabPane tab={`已签收(${tab8 || 0})`} key="8"></TabPane>
          <TabPane tab={`已取消(${tab9 || 0})`} key="9"></TabPane>
        </Tabs>
      </div>
    )
  }
}



